.Gradient-BG {
  width: 100%;
  height: 130vw;
  padding: 25px 0px;
  color: #fff;
  background: -webkit-linear-gradient(135deg, #f39c12, #16a085, #8e44ad, #e74c3c);
  background: linear-gradient(-45deg, #f39c12, #16a085, #8e44ad, #e74c3c);
  background-size: 400% 400%;
  -webkit-animation: Gradient 4s ease infinite;
          animation: Gradient 4s ease infinite; }

.Position {
  display: flex;
  flex-flow: column;
  align-items: center;
  justify-content: space-around; }

@-webkit-keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@keyframes Gradient {
  0% {
    background-position: 0% 50%; }
  50% {
    background-position: 100% 50%; }
  100% {
    background-position: 0% 50%; } }

@media (min-width: 499px) {
